@import "../../../assets/styles/variables.scss";

.Contact{
    padding: 15px;
    color: white;
    .contact-title{
        font-size: 1.5rem;
        padding-bottom: 15px;
        text-align: right;
    }
    .contact-element{
        display: flex;
        text-align: right;
        padding: 5px 0px;
        .data{
            width: 80%;
            .title{
                font-size: 1rem;
                color: $font-color;
            }
            .content{
                font-size: 1rem;
                color: white;
                margin-bottom: 10px;
            }
        }
        
        .contact-icon{
            width: 20%;
            text-align:center;
            line-height: 40px;
            .contact-icon-element{
                color : $font-color;
                font-size: 25px;
            }
        }
        
    }
    .contact-element:last-of-type{
        margin-bottom: 20px;
    }
}

